@import-normalize;
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
  font-style: italic;
  height: auto;
}

.align-left {
  text-align: left !important;
}

.align-right {
  text-align: right !important;
}

.align-center {
  text-align: center !important;
}

.valign-top {
  vertical-align: top !important;
}

.valign-middle {
  vertical-align: middle !important;
}

.valign-bottom {
  vertical-align: bottom !important;
}

.is-hidden {
  display: none !important;
}

.sr-only,
.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

:root {
  --primary: #d40b01;
  --secondary: #1e3944;
}

html {
  font: 0.625em/1.5 arial, helvetica, sans-serif;
  color: #333;
}

body {
  font-size: 1.4rem;
  margin: 0;
  padding: 0;
  background: radial-gradient(circle at center, #a4d474, #222c18);
  min-height: 100vh;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #333;
}
a:hover, a:focus, a:active {
  text-decoration: underline;
}

.bs {
  overflow: hidden;
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  align-content: flex-start;
}

.bs__target {
  display: none;
}

.bs__start {
  position: relative;
  z-index: 2;
  margin: 0;
  padding: 3rem;
  border-radius: 3rem;
  border: 0;
  background: var(--secondary);
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
  animation: scale 1s ease-in infinite alternate;
  transition: all 150ms;
}
.bs__start:hover {
  border-radius: 10px;
}
.bs__is-playing .bs__start {
  display: none;
}

.bs__score {
  position: fixed;
  background: var(--secondary);
  border-radius: 3rem;
  padding: 2rem;
  top: 2rem;
  right: 2rem;
  color: #fff;
  font-size: 5rem;
  opacity: 0;
  transition: all 500ms;
}
.bs__is-playing .bs__score {
  opacity: 1;
}

.bs__version {
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 1rem;
  font-size: 1rem;
}